import styled from 'styled-components';
import { string, arrayOf, exact, bool } from 'prop-types';
import { BoxHeading } from '../../components';
import Item from './Item';

const Container = styled.div`
  width: 100%;
  height: 100%;
  padding: 80px 0;
  ${props => props.styles}
`;

const Items = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding-left: ${props => (props.first ? '80px' : 0)};
  padding-right: ${props => (props.last ? '80px' : 0)};
`;

const Timeline = props => (
  <Container styles={props.styles} className="timeline">
    <BoxHeading title={props.title} content={props.content} withPadding>
      <Items first={props.first} last={props.last} className="items">
        {props.items.map((item, i) => {
          const first = props.first && i === 0;
          const last = props.last && i === props.items.length - 1;
          return <Item key={item.date} {...item} first={first} last={last} />;
        })}
      </Items>
    </BoxHeading>
  </Container>
);

Timeline.propTypes = {
  title: string,
  content: string,
  first: bool,
  last: bool,
  items: arrayOf(
    exact({
      image: string,
      date: string,
      title: string,
      content: string,
    }),
  ),
  styles: arrayOf(string),
};

export default Timeline;
