import styled from 'styled-components';
import { node, oneOf, string, bool, exact, object } from 'prop-types';
import BoxHeader from './BoxHeader';

const Content = styled.p`
  text-align: ${props => props.align};
  margin-top: 80px;
  padding: ${props => (props.withPadding ? '0 80px' : 0)};
`;

const Wrapper = styled.div`
  width: 100%;
  height: 680px;
  margin-top: 64px;
`;

const BoxHeading = props => (
  <>
    {props.title && <BoxHeader align={props.align}>{props.title}</BoxHeader>}
    {props.content && (
      <Content align={props.align} withPadding={props.withPadding} className="content">
        {props.content}
      </Content>
    )}
    <Wrapper className="wrapper">{props.children}</Wrapper>
  </>
);

BoxHeading.propTypes = {
  children: node.isRequired,
  content: string,
  title: string,
  withPadding: bool,
  align: oneOf(['left', 'center']),
};

BoxHeading.defaultProps = {
  align: 'left',
};

export default BoxHeading;
